#page-landing {
    height: 100vh;
    width: 100vw;
    background: linear-gradient(329.54deg, #29B6D1 0%, #00C7C7 100%);

    display: flex;
    justify-content: center;
    align-items: center;
}

#page-landing .content-wrapper {
    position: relative; /* relative para que os elementos de Estado e Cidade se mantenham relativo a estes*/

    width: 100%;
    max-width: 1100px;
    height: 100%;
    max-height: 680px;

    display: flex;
    align-items: flex-start;
    flex-direction: column; /* um abaixo do outro*/
    justify-content: space-between; /* mantenha espacos iguais entre eles*/

    background: url('../../images/landing.svg') no-repeat 80% center;
}

#page-landing .content-wrapper main {
    max-width: 350px; /* para forçar quebra de linha */
}

#page-landing .content-wrapper main h1 {
    font-size: 76px;
    font-weight: 900px;
    line-height: 70px;
}

#page-landing .content-wrapper main p {
    margin-top: 40px; /* para distanciar do titulo*/;
    font-size: 24px;
    line-height: 34px;
}

.content-wrapper .location {
    position: absolute;
    right: 0;
    top: 0;

    font-size: 24px;
    line-height: 34px;

    display: flex;
    flex-direction: column;

    text-align: right;
}

.content-wrapper .location strong {
    font-weight: 800;
}

.content-wrapper .enter-app {
    position: absolute;
    right: 0;
    bottom: 0;

    width: 80px;
    height: 80px;
    background-color: #FFd666;
    border-radius: 30px;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: background-color 0.2s; /* para que a troca de cor não seja seca, adiciona delay */
}

.content-wrapper .enter-app:hover {
    background-color: #96FEFF;
}
